import './styles/menu.css';

function Men(prop) {
    return (
        <div className="cup" >
            <h1 className="ptitle" > {prop.title} </h1>
            <h6 className="author" >{prop.author} </h6>
            <h6 className="date" >{prop.date} </h6>
            <h6 className="share" >share this post</h6>
            <h6 className="intro" >{prop.intro}</h6>
            <h6 className="peep" >{prop.peep}</h6>
            <img src={prop.image} alt="lve" className="image" ></img>
            <h5 className="last" >{prop.last}</h5>


        </div>
    )
}

export default Men

