import Men from "./men"
import bam from "./angel.jpg"

function Life() {
    return (
        <div className="pop" >
            <Men
             title="life as a youth living in lagos in 2021" 
             author="anu - life"
             date="november 5,2021"
             intro="life: a series on the lifestyle of an average  nigerian youths "
            peep="our guest on today's 'life' series is angel"
            image={bam}
            last="angel"
            ></Men>
        </div>
    )
}

export default  Life